import styles from './Data.module.css';

const Data = (props)=>{
    let data = null;
        data = Object.keys(props.data.data).map(key=>{
            return(
                <div key={key} className={styles.tableStyle}>
                    <h2>{key}</h2>
                    <table>
                        <tbody>
                            <tr>
                                {
                                    Object.keys(props.data.data[key][0]).map(heading=>{
                                        return(
                                            <th key={heading}>{heading}</th>
                                        )
                                    })
                                }
                            </tr>
                            {
                                props.data.data[key].map((key1 , index)=>{
                                    return(
                                        <tr key={key1 + index} >
                                            {
                                                Object.keys(key1).map((data , index1)=>{
                                                    // if(data == 'id') return;
                                                    return(
                                                        <td key={key1[data] + index + index1}>{key1[data]}</td>
                                                    )
                                                })
                                            }
                                        </tr>
                                    )
                                })
                            }
                        </tbody>
                    </table>
                </div>
            )
        });
    
    return(
        <div className={styles.Data}>
            <div>
                {data}
            </div>
        </div>
    );
};

export default Data;